/* alumni-sans-100 - cyrillic_latin */
@font-face {
  font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */
  font-family: 'Alumni Sans';
  font-style: normal;
  font-weight: 100;
  src: url('./alumni-sans-v16-cyrillic_latin-100.eot'); /* IE9 Compat Modes */
  src: url('./alumni-sans-v16-cyrillic_latin-100.eot?#iefix')
      format('embedded-opentype'),
    /* IE6-IE8 */ url('./alumni-sans-v16-cyrillic_latin-100.woff2')
      format('woff2'),
    /* Super Modern Browsers */ url('./alumni-sans-v16-cyrillic_latin-100.woff')
      format('woff'),
    /* Modern Browsers */ url('./alumni-sans-v16-cyrillic_latin-100.ttf')
      format('truetype'),
    /* Safari, Android, iOS */
      url('./alumni-sans-v16-cyrillic_latin-100.svg#AlumniSans') format('svg'); /* Legacy iOS */
}
